.animationdesection {
	margin: 0;
	--nav-load-time: 300ms;
	--nav-link-load-time: 500ms;
	--article-load-delay: calc(var(--nav-load-time) + var(--nav-link-load-time))
}


.animationdesection {
	animation: article-load 300ms ease-in var(--article-load-delay);
	animation-fill-mode: forwards;
	transform: translateY(-20px);
	opacity: 0;
}

p {
	animation: article-load 300ms ease-in calc(var(--article-load-delay) + 200ms);
	animation-fill-mode: forwards;
	transform: translateY(-20px);
	opacity: 0;
}

@keyframes nav-load {
	0% {
		transform: translateY(-100%);
	}

	100% {
		transform: translateY(0);
	}
}

@keyframes nav-link-load {
	0% {
		transform: scale(0);
	}

	90% {
		transform: scale(1.1);
	}

	100% {
		transform: scale(1);
	}
}

@keyframes nav-first-link-load {
	0% {
		transform: translateX(-1000%);
	}

	90% {
		transform: translateX(50%);
	}

	100% {
		transform: translateX(0);
	}
}

@keyframes nav-last-link-load {
	0% {
		transform: translateX(1000%);
	}

	90% {
		transform: translateX(-50%);
	}

	100% {
		transform: translateX(0);
	}
}

@keyframes article-load {
	0% {
		transform: translateY(-20px);
		opacity: 0;
	}

	100% {
		transform: translateY(0px);
		opacity: 1;
	}
}


.rond,
.souris {

	transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}


.faq__items--p {
	transition: 0.25s ease-in-out;
}

.faq__items--title {
	transition: all 0.25s ease-out;
}


.faq__items--title::after {

	transform: rotate(135deg);
	transition: 0.35s ease-in-out;

}

.faq__items--btn:checked+.faq__items--title::after {
	transform: rotate(-45deg);
	transition: 0.25s ease-in-out;
}

.credits__item {

	transition: box-shadow .3s ease, transform .2s ease;
}


.credits__item--card {

	transition: transform .2s ease, opacity .2s ease;
}

.credits__item--pic {

	transition: transform .2s ease;

}

.credits__item--pictos {
	transform: translateY(200%);

	transition: transform .2s ease, opacity .2s ease;
}

.credits__item--picto svg {

	transition: fill 0.2s ease, transform 0.2s ease;
}


.credits__item:hover .credits__item--card {
	transform: translateY(-5%);
}

.credits__item:hover .credits__item--pictos {
	transform: translateY(100%);
	opacity: 1;
}

.credits__item--picto svg:hover {
	fill: $c-link-hover;
	transform: scale(1.1);
}

.credits__item--pic:hover {
	transform: scale(1.1);
}

@media (max-width: 700px) {
	.background {
		transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);

	}

	.nav {
		transition: all 0.8s;
	}

	.link:link,
	.link:visited {
		transition: all 0.4s;
	}

	.checkbox:checked~.background {
		transform: scale(80);
	}

	.icon::before,
	.icon::after {
		transition: all 0.2s;
	}


	.list {

		transform: translate(-50%, -50%);

	}

	.icon {

		transform: translate(-50%, -50%);
	}
}