body,
.active,
.buttoncolors,
.icon,
.icon::before,
.icon::after,
.contentonglet,
.card,
.contentonglet__about,
.contentonglet__cheatsheet {
	background-color: $c-bg;

}

.link__white:hover {
	color: $c-link-hover;

}

.link__white:focus,
.link__white:active,
.link__white:visited {
	color: $c-link-vfa;
}


.foot,
header,
.foot__deux {
	background-color: $c-footer;
}

.foot,
.foot__deux {
	border-top: 0.03px solid $c-border;
}


header {
	border-bottom: 0.03px solid $c-border;

}

.activenav {
	text-decoration: underline;
}

.burger {

	box-shadow: 0 1rem 3rem $c-shadow;
}

.background,
.burger {

	background: $c-link-hover;


}

body,
.link__green,
.credits__item--name,
.card-subtitle,
.logo,
.link__white,
.logo:hover,
.buttoncolors,
.cards__link {
	color: $c-text;
}


.rond,
.souris {
	background: $c-text;
}


.logo:hover {
	text-decoration: none;

}

.link,
.link:link,
.link:visited,
.icon,
.icon::before,
.icon::after,
.link__green:hover,
.card:hover,
.buttoncolors:hover,
.buttoncolors.active,
.active,
.buttoncolors:hover,
.buttoncolors:active {
	color: $c-bg;
}


.link:hover,
.link:active {
	color: $c-nav-hover;
}


.bas__btn {
	background-color: transparent;
	background-repeat: no-repeat;
	border-color: none;
}


.picture {
	background-color: transparent;
	color: $c-text;
	border: 1px solid $c-text;
}


.contentonglet {
	width: 90%;
	margin: auto;
	border-radius: 10px;
	padding: 2%;
}


.buttoncolors {
	border: 1px dashed $c-text;
	color: $c-text;
}


.buttoncolors:hover {
	background-color: $c-link-hover;
}

.buttoncolors.active {
	background-color: $c-link-hover;
}


.picture {

	background-color: transparent;
	border: 1px solid $c-text;

	&:hover,
	&:focus {
		background-color: $c-bg;
		box-shadow: 0 0 0 4px $c-shadow, 0 0 0 5px $c-shadow;
	}
}


.card {
	border: 1px solid $c-text;

	color: $c-text;

	&:hover {
		color: $c-bg;
		background-color: $c-link-hover;
		transform: translateY(-5px);
		border: 1px solid $c-bg;
	}

	&:focus-within {
		box-shadow: 0 0 0 2px $c-shadow, 0 0 0 4px $c-shadow;
	}

	&:nth-child(2) {
		background-color: $c-bg;

		&:focus-within {
			box-shadow: 0 0 0 2px $c-shadow, 0 0 0 4px $c-shadow;
		}
	}

	&:nth-child(3) {
		background-color: $c-bg;

		&:focus-within {
			box-shadow: 0 0 0 2px $c-shadow, 0 0 0 4px $c-shadow;
		}
	}


}


.contentonglet {


	box-shadow: 0px 5px 15px $c-shadow;

}


.buttoncolors {

	border: 1px dashed $c-text;

}


.buttoncolors:hover,
.buttoncolors.active {
	background-color: $c-link-hover;

}


.gradient {


	background: transparent;
	background: linear-gradient(-45deg, #ffffff 0%, #9B9B9B 100%);
	background-clip: text;
	-webkit-text-fill-color: transparent;
	background-clip: text;
}

.faq__items--title {
	border-top: $c-text 0.001px solid;
}

.faq__items--title:hover {
	color: $c-link-hover ;
}


table {
	color: $c-text;
	border: 1px solid $c-text;
}


table tr {
	background-color: $c-bg;
	border: 1px solid $c-text;
}


@media screen and (max-width: 700px) {
	table tr {
		border-bottom: 3px solid $c-text;

	}

	table td {
		border-bottom: 1px solid $c-text;

	}

}

.green {
	color: $c-table;
}


.credits__item--picto svg {

	fill: $c-text;
}


.credits__item:hover {
	box-shadow: 0 8px 50px $c-shadow;
}


@media (min-width: 700px) {

	input {

		border: 4px solid $c-text;

	}

	.fermer:after {
		content: "";
		background-color: $c-text;
	}

	.box {
		border: 4px solid $c-text;
		color: $c-text;
	}


	.search:before {
		content: "";
		background-color: $c-text;
	}


	.box {

		border: 4px solid $c-text;


	}
}
