body {
	height: 100%;
	width: 100%;
	max-width: 1350px;
	margin: 0 auto;
	scroll-behavior: smooth;
}

.uno {
	padding-bottom: 5%;
}

.contentlink {
	display: none;
	white-space: nowrap;
	overflow: hidden;
	text-indent: 100%;
}

.foot,
.foot__deux,
header {
	display: flex;
	justify-content: space-between;
}

.contentonglet__about{
	padding: 1em;
}

.about, .selectors {    
	  content: "";
      background-image: url(https://images.pexels.com/photos/7599547/pexels-photo-7599547.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2);
      background-size: cover;
      top: 0px;
      right: 0px;
      bottom: 0px;
      left: 0px;
      opacity: 0.5;
}



.foot {
	padding: 50px;
	flex-direction: column;
	align-items: center;
}

.social {
	padding: 0px;
}

.accroche {
	padding-top: 1%;
	padding-bottom: 1%;
	padding-left: 5%;
	padding-right: 8%;

}


.foot__deux {
	padding: 10px;
}

.checkbox {
	display: none;
}

.bas__btn {
	background-image: url(../assets/images/top.svg);
	display: inline-block;
	background-size: contain;
	background-color: transparent;
	background-repeat: no-repeat;
	border-color: none;
	white-space: nowrap;
	overflow: hidden;
	text-indent: -9999%;
	border: none;
	width: 30px;
	height: 30px;
}

.picto {
	width: 16px;
	height: 16px;
}

.uppercase {
	text-transform: uppercase;
}

.rond,
.souris {
	position: fixed;
	border-radius: 20%;
	pointer-events: none;
}

.souris {
	height: 5px;
	width: 5px;
	z-index: 10;
}

.rond {
	height: 20px;
	width: 20px;
	mix-blend-mode: difference;
	z-index: 11;
}

.picture {
	width: 32px;
	height: 32px;
	border-radius: 50%;
	border: 0;
	background-image: url(../assets/images/top.svg);
	background-color: transparent;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: 0.25s ease;
	flex-shrink: 0;

	&.large {
		width: 42px;
		height: 42px;
		font-size: 1.25em;
	}

}

.cards {

	display: grid;
	grid-template-columns: repeat(3, 1fr);
	column-gap: 1rem;
	row-gap: 1rem;

}


.card {

	padding: 1rem;
	border-radius: 8px;


	min-height: 100px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	position: relative;
	transition: 0.25s ease;


	.credits {

		display: flex;
		align-items: center;
		justify-content: space-between;
		font-weight: 600;

		.picture {
			color: inherit;
			border-color: inherit;

			&:hover,
			&:focus {
				background-color: transparent;
			}
		}

		&:focus {
			box-shadow: none;
		}

		&:after {
			content: "";
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
		}
	}
}

.card__head {
	display: flex;
	align-items: center;

	h3 {
		display: flex;
		flex-direction: column;
		line-height: 1.375;
		margin-left: 0.5rem;

		span:first-child {
			font-weight: 600;
		}

		span:last-child {
			font-size: 0.825em;
			font-weight: 200;
		}
	}
}


.contentonglet {
	width: 90%;
	margin: auto;

	border-radius: 10px;

	padding: 2%;
	margin-top: 3%;
}

.buttonContenttabs {


	margin-bottom: 1rem;
	padding: 10px;
}

.buttoncolors {
	letter-spacing: 3px;
	border-radius: 10px;
	text-decoration: none;

	transition: 0.5s;
}

.onglet__btn {
	margin-left: 10px;
	padding-left: 30px;
	padding-right: 30px;
	padding-top: 10px;
	padding-bottom: 10px;
	margin-bottom: 10px;

}

.buttoncolors:hover {

	border: none;
	text-decoration: none;
}

.buttoncolors.active {

	border: none;
}

.active {

	border: none;
}

.cartes {
	text-align: left;
	padding: 10px;
}

.content {
	display: none;
	padding: 10px 20px;
}

.content.active {
	display: block;
}


.pictures {
	display: grid;
	grid-template-columns: 1fr 1fr;
}


.faq__items {
	margin-bottom: 30px;

}

.faq__items--p {
	max-height: 0px;
	overflow: hidden;
}

.faq__items--title {
	display: block;
	padding: 30px 0 0 0;
	position: relative;
	margin-bottom: 0;
}

.faq__items--title::after {
	border-style: solid;
	border-width: 1px 1px 0 0;
	content: " ";
	display: inline-block;
	margin: auto;
	height: 10px;
	left: 2px;
	position: relative;
	right: 20px;
	top: 2px;
	vertical-align: top;
	width: 10px;
}

.faq__items--btn:checked+.faq__items--title+.faq__items--p {
	max-height: 100%;
}


input[type="checkbox"] {
	display: none;
}


table {
	border-collapse: collapse;
	margin: 0;
	padding: 0;
	width: 100%;
	table-layout: fixed;
}

table caption {
	margin: .5em 0 .75em;
}

table tr {

	padding: .35em;
}

table th,
table td {
	padding: .625em;
}


.credits {
	display: flex;
	flex-direction: row;
}

.credits__item--descr {
	padding-right: 3em;
	padding-top: 3em;
	padding-left: 3em;
	padding-bottom: 3em;

}

.credits__item {
	padding: 3em;
	width: 100%;
	height: 100%;
}

.credits__item--card {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

}

.credits__item--pic {
	width: 60px;
	height: 60px;
	border-radius: 50%;

	margin-bottom: 1rem;
}

.credits__item--pictos {

	display: flex;
	justify-content: space-around;
	width: 100%;
	opacity: 0;

}

.credits__item--picto {
	list-style: none;
	margin-left: -85px;
}

.credits__item--picto svg {
	display: block;
	height: 18px;
	width: 18px;

}


.next {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	padding: 2em;
}

@media (max-width: 700px) {

	table {
		border: 0;
	}


	table thead {
		border: none;
		clip: rect(0 0 0 0);
		height: 1px;
		margin: -1px;
		overflow: hidden;
		padding: 0;
		position: absolute;
		width: 1px;
	}

	table tr {
		display: block;
		margin-bottom: .625em;
	}

	table td {

		display: block;
		font-size: .8em;
		text-align: right;
	}

	table td::before {
		content: attr(data-label);
		float: left;

	}

	table td:last-child {
		border-bottom: 0;
	}

	.credits__item {
		display: none;
	}

	.none {
		display: block;

	}

	.mobile {
		padding-top: 2em;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.logo {
		margin-top: 1%;
		margin-left: 2rem;
	}

	.contentonglet__cheatsheet {
		width: 90%;
		margin: auto;

		border-radius: 10px;

		padding: 2%;
		margin-top: 3%;
	}


	.about {
		padding: 2em;
	}

	#searchcontenu {
		display: none;
	}


	.cards {
		grid-template-columns: repeat(1, 1fr);
	}


	.navdesktop {
		display: none;
	}


	.checkbox {
		display: none;
	}

	.burger {
		position: relative;
		height: 5rem;
		width: 5rem;
		border-radius: 50%;
		position: fixed;
		top: 1%;
		right: 1%;
		z-index: 3000;
		text-align: center;
	}

	.background {
		height: 4rem;
		width: 4rem;
		border-radius: 50%;
		position: fixed;
		top: 1.5vh;
		right: 1.5%;
		z-index: 2500;

	}

	.nav {
		height: 100vh;
		position: fixed;
		top: 0;
		right: -60%;
		z-index: 2500;
		opacity: 0;
		width: 0;
	}

	.list {
		position: absolute;
		top: 50%;
		left: 50%;
		list-style: none;
		text-align: center;
		width: 100%;
	}

	.item {
		margin: 1rem;
		font-size: 36px;
	}

	.link,
	.link:visited {
		display: inline-block;
		font-size: 2rem;
		font-weight: 300;
		background-size: 220%;
	}


	.checkbox:checked~.nav {
		opacity: 1;
		width: 100%;
		right: 0;
	}

	.icon {
		position: absolute;
		top: 50%;
		left: 50%;
	}

	.icon,
	.icon::before,
	.icon::after {
		width: 3rem;
		height: 2px;
		display: inline-block;
	}

	.icon::before,
	.icon::after {
		content: "";
		position: absolute;
		left: 0;
	}

	.icon::before {
		top: -0.8rem;
	}

	.icon::after {
		top: 0.8rem;
	}

	.burger:hover icon:before {
		top: -1rem;
	}

	.burger:hover icon::after {
		top: 1rem;
	}

	.checkbox:checked+.burger .icon {
		background-color: transparent;
	}

	.checkbox:checked+.burger .icon::before {
		top: 0;
		transform: rotate(135deg);
	}

	.checkbox:checked+.burger .icon::after {
		top: 0;
		transform: rotate(-135deg);
	}
}


@media (min-width: 700px) {
	.none {
		display: none;
	}

	.logo {
		margin-top: 1%;
		margin-left: 3rem;
	}

	.allfaq {
		padding: 1em;
	}

	.contentonglet__cheatsheet {
		width: 70%;
		margin: auto;

		border-radius: 10px;

		padding: 2%;
		margin-top: 3%;

	}


	.contentonglet__about {
		width: 67%;
		margin: auto;
		margin-right: 33%;
		margin-left: 4em;
		border-radius: 10px;
		padding-left: 2%;
		margin-top: 3%;
		padding-bottom: 3%;
	}


	#searchcontenu {

		position: sticky;
		height: 50px;
		margin-left: 98%;
		margin-top: -3.5rem;


	}

	input {
		box-sizing: border-box;
		width: 30px;
		height: 30px;
		border-radius: 50%;
		background: none;
		outline: 0;

	}

	.search {
		background: none;
		position: absolute;
		top: 0px;
		left: 0;
		height: 30px;
		width: 30px;
		padding: 0;
		border-radius: 100%;
		outline: 0;
		border: 0;
		color: inherit;

	}

	.search:before {
		content: "";
		position: absolute;
		width: 20px;
		height: 4px;


		margin-top: 14px;
		margin-left: 6px;

	}

	.fermer:before {
		content: "";
		position: absolute;
		width: 17px;
		height: 4px;
		margin-top: -1px;
		margin-left: -13px;

	}

	.fermer:after {
		content: "";
		position: absolute;
		width: 17px;
		height: 4px;

		margin-top: -1px;
		margin-left: -13px;
		cursor: pointer;


	}

	.box {
		box-sizing: border-box;
		padding: 0 40px 0 10px;
		width: 300px;
		height: 30px;
		border-radius: 20px;
		background: none;
		outline: 0;

	}

	.foot {
		flex-direction: row;
		align-items: baseline;
	}

	.navdesktop {
		display: flex;
		flex-direction: row;
		align-items: baseline;
		gap: 30px;
		margin-left: auto;
		margin-right: 5rem;
		margin-top: 2rem;
	}

	.nav {
		display: none;
	}

	.hamburger {
		display: none;
	}


}