body {
	font-family: $type-normal;
	font-size: $size-text;
	line-height: $line-height-body;
}

h1,
h2,
h3,
a {
	font-family: $type-normal;
	line-height: $line-height-titles;
}

h1 {
	font-size: $size-h1;
}

h2 {
	font-size: $size-h2;
}

h3 {
	font-size: $size-h3;
}

small {
	font-size: $size-small;
}

a {
	text-decoration: none;
}

a:hover,
link:hover {
	text-decoration: underline;
}

li {
	list-style-type: none;
}


.accroche {
	font-family: $type-accroche;
}

.logo {
	font-size: $size-h3;
	font-family: $type-accroche;
}

.uppercase {
	text-transform: uppercase;
}


button {
	font-size: $size-text;
}


.faq__items--title,
.picture,
.bas__btn,
.buttoncolors,
.search,
a,
.burger {
	cursor: pointer;
}


.cards__link:hover {

	text-decoration: none;
}


.faq__items--p {
	font-size: 1.25em;
	line-height: 1.4em;

}

.faq__items--title {

	font-size: 1.25em;
	font-weight: 700;

}

table caption {
	font-size: 1.5em;
}

table th,
table td {
	text-align: center;
}

table th {
	font-size: .85em;
	letter-spacing: .1em;
	text-transform: uppercase;
}


.green {
	font-weight: bold;
}


.microtable {
	white-space: normal;
}

.credits__item--picto svg {

	cursor: pointer;
}

.credits__item--name {
	font-size: 1.5em;
	font-weight: 600;
	line-height: 2rem;
}

.card-subtitle {

	font-size: 0.8em;
}


@media (max-width: 700px) {
	table caption {
		font-size: 1.3em;
	}

	table td::before {
		content: attr(data-label);

		font-weight: bold;
		text-transform: uppercase;
	}


}

@media (min-width: 700px) {

	.contentonglet__about {

		font-size: $size-h4;

	}

	.box {
		font-size: $size-small;

		font-weight: 400;


	}

}